
/* /////////////// CONTENT CARDS /////////////////// */
// flexbox source https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Flexible_Box_Layout/Mastering_Wrapping_of_Flex_Items
section#content {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: auto;
  
    div.group {
      display: flex;
      flex-direction: column;
      margin: 36px;
     div.group-title {
  
        background-color: $dark-white;
        padding: 12px 0;
        border-radius: $big-radius;
        display: flex;
        justify-content: flex-start;
        h3, input {
            font-weight: 500;
            font-size: 16px;
            color: $dark-grey
        }
  
        input {
          width: auto;
          background-color: transparent;
          border: none;
          padding: 2px;
          text-decoration: underline dotted transparent;
        }
  
        input:focus {
          text-decoration: none;
        }
  
        input.column-name:hover {
          cursor: text;
          text-decoration: underline dotted $dark-grey;
        }
    }
      div.tiles {
        display: flex;
        flex-wrap: wrap;
        margin:-12px;
        article.tile {
          min-width: 260px;
          max-width: 280px;
          flex: 1 1 280px;
          margin: 12px;
          border-radius: $big-radius;
          background-color: $white;
          padding: 16px;
          filter: drop-shadow(0 0 2px #30303018);
        }
      }
    }
  }
  
  