.background {
    display: none;
     background-color: #0e1318af;
     position: fixed;
     top: 0;
     left: 0;
     width: 100vw;
     height: 100vh;
     z-index: 5;
   }
.edit:hover {
    background-color: greenyellow;
    cursor: pointer;
  }
  
  .edit {
    padding: 18px;
  }
  
  form#create-task-form {
    button#create-task-submit {
      display: flex;
    }
    button#edit-task-save, button#edit-task-cancel, button#edit-task-delete {
      display: none;
    }
  }
  form#create-task-form.update {
    button#create-task-submit {
      display: none;
    }
    button#edit-task-save, button#edit-task-cancel, button#edit-task-delete {
      display: flex;
    }
  }
/* false stuff */

label, button#create-task-submit {
    display: block;
    margin-top: 1em;
  }
  

  // FORM
form {
    z-index: 8;
  }
  #create-task-form, #add-column-form, #create-content-form {
    display: none;
    position: absolute;
    width: 80%;
    height: 80%;
    top: calc(10% - 18px);
    left: calc(10% - 18px);
    padding: 36px;
    flex-direction: column;
    background-color: $white;
    border-radius: $big-radius;
    input {
      border: none;
      filter: drop-shadow(0 0 2px #30303018);
      border-radius: $mini-radius;
      padding: 12px;
    }
    input:focus {
      border: none;
      filter: drop-shadow(0 0 2px #30303048);
    }
  
    input[type=radio] {
      width: 0;
    }
    label.false-radio {
      span {
        background-color: $white;
        color: $dark-grey;
        padding: 16px;
      }
    }
  
    label.false-radio:hover{
      span {
        color: $black;
      }
      cursor: pointer;
    }
    input[type=radio]:checked ~ span {
      background-color: $black;
      color: $white;
    }
    label[for=taskPriorityRating] {
      display: flex;
      flex-direction: row;
    }
  }
  #create-task-form.active, #add-column-form.active, #create-content-form.active {
    display: flex;
  }